<template>
  <div class="artist">
    <div class="mark"></div>
    <div class="top">
      <img :src="briefDesc.cover" alt="" />
      <div class="content">
        <div>{{ briefDesc.name }}</div>
        <div v-if="toplist">歌手榜华语地区No.{{ toplist }}</div>
      </div>
    </div>
    <h3>歌手简介</h3>
    <span
      v-html="briefDesc.briefDesc"
      class="jianjie"
      :class="{ active: showbBriefDesc ? true : false }"
    ></span>
    <span class="xia" @click.stop="showbBriefDesc = !showbBriefDesc">
      {{ showbBriefDesc ? "完整歌手介绍" : "收起" }}
      <span class="iconfont icon-xiangxiajiantou"></span>
    </span>
    <h3>热门歌曲</h3>
    <music-item :musicitem="artistsSong"></music-item>
     <!-- <div   class="loadmore"
     v-if="showMore.length>0"
     ref="loadmore"
     @click.stop="CommentsMore">
        {{commentsPages>=CommentspagesAll?'已经到底了...':'点击加载更多...'}}
    </div> -->
  </div>
</template>

<script>
import MusicItem from "../components/MusicItem.vue";
export default {
  components: { MusicItem },
  props: {
    currentmusic: {
      type: Object,
      default: () => {
        return null;
      },
    },
    currentstate: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      allArtist: [], //歌手榜
      briefDesc: {}, //歌手详情
      showbBriefDesc: true,
      hotSongs: [],
    };
  },
  created() {
    this.getArtist();
    this.getArtistContent();
    this.getHotSongs();
  },
  methods: {
    //    歌手榜
    async getArtist() {
      await this.axios.get("/toplist/artist").then((res) => {
        console.log(res.list.artists);
        this.allArtist = res.list.artists;
      });
    },
    //    歌手详情
    async getArtistContent() {
      await this.axios
        .get("/artist/detail?id=" + this.$route.query.id)
        .then((res) => {
          console.log(res.artist);
          this.briefDesc = res.artist;
        });
    },
    getHotSongs() {
      this.axios.get("/artists?id=" + this.$route.query.id).then((res) => {
        console.log(res);
        this.hotSongs = res.hotSongs.map((item, index) => {
          item.song = {
            album: item.al,
            alias: item.alia,
            artists: item.ar,
            privilege: item.privilege,
            i: index + 1,
          };
          return item;
        });
      });
    },
  },
  computed: {
    toplist() {
      let uid = this.$route.query.id;
      let artistIndex = this.allArtist.findIndex((item) => {
        return item.id == uid;  
      });
      return artistIndex + 1;
     
    },
    artistsSong(){
      return this.hotSongs.slice(0,6);
    }
  },
};
</script>

<style lang="scss" scoped>
.artist {
  position: relative;
  .mark {
    width: 100%;
    height: 305px;
    background-color: rgba(0, 0, 0, 0.121);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }
  .top {
    position: relative;
    img {
      width: 100%;
      display: block;
    }
    .content {
      color: white;
      position: absolute;
      bottom: 64px;
      left: 20px;
      z-index: 2;
      div {
        height: 30px;
        line-height: 30px;
      }
    }
  }
  h3 {
    margin: 10px 10px 5px;
    font-size: 20px;
    font-weight: bold;
  }
  .jianjie {
    width: 100%;
    height: 90px;
    line-height: 20px;
    color: rgb(128, 128, 128);
    font-size: 14px;
    display: inline-block;
    text-indent: 2em;
    text-align: justify;
    padding: 10px;
    box-sizing: border-box;
    &.active {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 4;
      overflow: hidden;
    }
  }
  .xia {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 14px;
    letter-spacing: 2px;
    margin-top: 5px;
    span {
      font-size: 14px;
    }
  }
}
</style>